'use strict'

import * as functions from "./modules/functions.js";
import { startInit } from "./modules/counertanim.js"

document.addEventListener('DOMContentLoaded', (e) => {
	functions.isWebp();
	functions.burger();
	functions.ibg();
	functions.ripple();
	functions.filter();
	ymaps.ready(functions.map);
})

window.addEventListener('load', () => {
	startInit();
});
